.index-div {
    margin: 2rem;
    padding: 1rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.profile-image {
    width: 8rem;
    height: 10rem;
    margin-bottom: 1rem;
}

.sidebar {
    width: 12%;
    min-height:max-content;
    margin-right: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.sidebar-div-1 {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.link-page {
    text-decoration: none;
    color: black;
    padding: 1rem;
    width: 80%;
}

.link-page:hover {
    background-color: #9e4244;
    color: white;
}

.sidebar-div-2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 2.5rem 0;
}

.content {
    width: 85%;
}

.social-media-links, .social-media-links:focus, .social-media-links:active, .social-media-links:visited {
    color: black;
}

.social-media-links {
    padding: 6px;
}

.social-media-links:hover {
    color: white;
    background-color: #9e4244;
}